<Grid xs={12}>
<Typography variant='caption'>
{availability=='checked'&&availableRooms>0?availableRooms+' Rooms Availabile for selected date and '+selectedNights +' nights':availability=='checked'&&'Sorry no room available for selected date and '+selectedNights +' nights'}
</Typography>
</Grid>
